<script>
export default {
  data () {
    return {
      time: (new Date()).toLocaleTimeString(),
      color: true,
      act: 'outact',
      gp: 'lot-sort-hover',
      xy: false,
      gf: false,
      msg1: '1.老王嫖娼当场被抓',
      msg2: '2.根据警方审讯,因其快男找不到女朋友故而嫖娼',
      msg3: '3.警方表示这种人一天可以抓一大把',
      CarouselImg: 'img',
      cqssc: 'main-body-s-bet-head-hover',
      bjpk10: false,
      jslhc: false,
      txffx: false,
      xjffc: false,
      betMoney: 1,
      betAll: 5,
      sscOne: Math.floor(Math.random() * 10),
      sscTwo: Math.floor(Math.random() * 10),
      sscThree: Math.floor(Math.random() * 10),
      sscFour: Math.floor(Math.random() * 10),
      sscFive: Math.floor(Math.random() * 10),
      appleselect: 'codeselect',
      andorselect: false,
      messageHead: '适用于ios6.0及以上平台',
      messageBottom: '扫一扫二维码下载ios版'
    }
  },
  methods: {
    active: function () {
      // this._class = 'active'
      this.act = 'active'
    },
    outact: function () {
      this.act = 'outact'
      // this._class = 'outact'
    },
    change: function (id) {
      this.gp = false
      this.xy = false
      this.gf = false
      if (id === 'gp') {
        this.gp = 'lot-sort-hover'
      }
      if (id === 'xy') {
        this.xy = 'lot-sort-hover'
      }
      if (id === 'gf') {
        this.gf = 'lot-sort-hover'
      }
      // lot-sort-hover
    },
    select: function (param) {
      this.bjpk10 = false
      this.jslhc = false
      this.txffx = false
      this.xjffc = false
      this.cqssc = false
      if (param === 'bjpk10') {
        this.bjpk10 = 'main-body-s-bet-head-hover'
      }
      if (param === 'jslhc') {
        this.jslhc = 'main-body-s-bet-head-hover'
      }
      if (param === 'txffx') {
        this.txffx = 'main-body-s-bet-head-hover'
      }
      if (param === 'xjffc') {
        this.xjffc = 'main-body-s-bet-head-hover'
      }
      if (param === 'cqssc') {
        this.cqssc = 'main-body-s-bet-head-hover'
      }
    },
    minut: function () {
      if (this.betMoney <= 0) {
        this.betMoney = 0
        this.betAll = 0
      } else {
        this.betMoney--
        this.betAll = this.betMoney * 5
      }
    },
    bplus: function () {
      this.betMoney++
      this.betAll = this.betMoney * 5
    },
    allMoney: function () {
      if (this.betMoney === '') {
        this.betMoney = 0
      }
      this.betAll = this.betMoney * 5
    },
    randomBet: function () {
      this.sscOne = Math.floor(Math.random() * 10)
      this.sscTwo = Math.floor(Math.random() * 10)
      this.sscThree = Math.floor(Math.random() * 10)
      this.sscFour = Math.floor(Math.random() * 10)
      this.sscFive = Math.floor(Math.random() * 10)
    },
    codeselect: function (param) {
      this.appleselect = false
      this.andorselect = false
      /* messageHead: '适用于ios6.0及以上平台',
        messageBottom: '扫一扫二维码下载ios版' */
      if (param === 'appleselect') {
        this.appleselect = 'codeselect'
        this.messageHead = '适用于ios6.0及以上平台'
        this.messageBottom = '扫一扫二维码下载ios版'
      }
      if (param === 'andorselect') {
        this.andorselect = 'codeselect'
        this.messageHead = '适用于Android4.0及以上平台'
        this.messageBottom = '扫一扫二维码下载Android版'
      }
    }
    // Math.floor(Math.random()*10)均值获取0-9
  },
  created () {
    var i = 0
    var s = 1
    var _this = this
    var speace = ' ' + ' ' + ' ' + ' ' + ' '
    _this.msg1 = _this.msg1 + speace + _this.msg2 + speace + _this.msg3
    var length = _this.msg1.length * 12
    // console.log(length)
    /* 时间更新 */
    setInterval(function () {
      var date = new Date()
      _this.time = date.toLocaleTimeString()
    }, 1000)
    /* 注册闪烁 */
    setInterval(function () {
      i++
      if (i % 2 === 0) {
        _this.color = true
      } else {
        _this.color = false
      }
    }, 250)
    /* 走马灯 */
    setInterval(function () {
      s = s + 1
      var rs = document.getElementById('main-body-notice-scroll-name')
      rs.style.left = 610 - s + 'px'
      var s1 = rs.style.left.substring(0, rs.style.left.length - 2) - 0
      // console.log(s1)
      if (-s1 > length) {
        s = 0
        rs.style.left = 610 - s + 'px'
      }
      // console.log(i)
      // console.log(rs.style.left)
    }, 25)
  }
}
